@mixin hover {
  &:hover { @content }
}

// sass-lint:disable brace-style
@mixin hover-focus {
  &:focus,
  &:hover {
    @content
  }
}

// 设置元素本身与 hover focus 一样的效果，使之看上去没有效果
@mixin plain-hover-focus {
  &,
  &:focus,
  &:hover {
    @content
  }
}

// hover focus active 统一样式
@mixin hover-focus-active {
  &:focus,
  &:active,
  &:hover {
    @content
  }
}
