@keyframes ripple-radius-in {
  from {
    transform: translate(var(--ripple-translate-start, 0)) scale(1);
    animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  to {
    transform: translate(var(--ripple-translate-end, 0)) scale(var(--ripple-scale, 1));
  }
}

@keyframes ripple-radius-out {
  from {
    transform: translate(var(--ripple-translate-end, 0)) scale(var(--ripple-scale, 1));
  }
  to {
    transform: translate(var(--ripple-translate-start, 0)) scale(1);
    animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
}

@keyframes ripple-opacity-in {
  from {
    opacity: 0;
    animation-timing-function: linear;
  }
  to {
    opacity: 1;
  }
}

@keyframes ripple-opacity-out {
  from {
    opacity: 1;
    animation-timing-function: linear;
  }
  to {
    opacity: 0;
  }
}

.ripple {
  position: relative;
  overflow: hidden;
  outline: none;
  will-change: transform, opacity;
  -webkit-tap-highlight-color: transparent;
}

.ripple:not(.ripple-impact):focus::before, .ripple:not(.ripple-impact):hover::before, .ripple:not(.ripple-impact):active::after {
  opacity: 0.6;
  transition-duration: 85ms;
}

.ripple::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.08);
  position: absolute;
  top: calc(50% - 100%);
  left: calc(50% - 100%);
  width: 200%;
  height: 200%;
  border-radius: 50%;
  pointer-events: none;
  opacity: 0;
  transition: opacity 250ms linear;
}

.ripple::after {
  content: "";
  background-color: rgba(0, 0, 0, 0.08);
  position: absolute;
  top: calc(50% - 100%);
  left: calc(50% - 100%);
  width: 200%;
  height: 200%;
  border-radius: 50%;
  pointer-events: none;
  opacity: 0;
  transition: opacity 250ms linear;
}

.ripple-impact {
  --ripple-width: 0;
  --ripple-height: 0;
  --ripple-size: 0;
  --ripple-left: 0;
  --ripple-top: 0;
  --ripple-scale: 1;
  --ripple-translate-end: 0;
  --ripple-translate-start: 0;
}

.ripple-impact.ripple-impact-focused::after, .ripple-impact:active::after {
  opacity: .6;
  transition-duration: 85ms;
}

.ripple-impact::before {
  top: 0;
  left: 0;
  width: 100%;
  width: var(--ripple-size, 100%);
  height: 100%;
  height: var(--ripple-size, 100%);
  transform: scale(0);
  transform-origin: center center;
}

.ripple-impact.ripple-impact-active::before {
  animation: 300ms ripple-radius-in forwards, 83ms ripple-opacity-in forwards;
}

.ripple-impact.ripple-impact-inactive::before {
  animation: 300ms ripple-radius-out forwards, 83ms ripple-opacity-out forwards;
}

.ripple-unbounded {
  overflow: visible;
}

.ripple-unbounded::after {
  top: calc(50% - 50%);
  top: var(--ripple-top, calc(50% - 50%));
  left: calc(50% - 50%);
  left: var(--ripple-left, calc(50% - 50%));
  width: 100%;
  width: var(--ripple-size, 100%);
  height: 100%;
  height: var(--ripple-size, 100%);
}

.ripple-impact.ripple-unbounded::after {
  transform: scale(0);
  transform: scale(var(--ripple-scale, 0));
}

.ripple-impact.ripple-unbounded::before {
  top: 0;
  top: var(--ripple-top, 0);
  left: 0;
  left: var(--ripple-left, 0);
}

